body {
  margin: 0;
  padding: 0;
  padding-top: 40px;
  font-family: sans-serif;
  background: #ddd;
}

.glyphicon:before {
  font-size: 24px;
  position: relative;
  top: 4px;
}

.player-type {
  padding: 12px;
}

.menu-title {
  margin-top: 40px;
  margin-bottom: 50px;
}

.start-game-button-container,
.selection-button-container {
  text-align: center;
  margin-bottom: 40px;
}

.glyphicon {
  padding: 15px;
}

.btn {
  padding: 30px;
  background: #ddd;
}

.start-game-button,
.start-game-button:hover,
.game-button,
.game-button:hover {
  background: #fff;
}

.continue-button,
.continue-button:hover {
  background: #555;
  color: #ddd;
  padding: 5px;
}

.return-button {
  margin-top: 30px;
}

.game-button > img {
  width: 30px;
}

@media (min-width: 400px) {
  .col-md-4 {
      width: 33.33333333%;
  }
}

@media (min-width: 400px) {
  .col-md-6 {
      width: 50%;
  }
}



